
import stickySlot from '../../components/StickySlot'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
name: "scroll2",
data () {
return {
tabItems: [
{status: "首页", id: 0},
{status: "宝贝", id: 1},
{status: "造物节", id: 2},
{status: "新品", id: 3},
{status: "实拍", id: 4},
{status: "买家秀", id: 5},
{status: "会员", id: 6}
],
index01: 0,
index: 0,
}
},
methods: {
switchTabItem (index) {
console.log('on-before-index-change', index)
if(index === 0) {
path: "scroll"
}
this.$vux.loading.show({
text: 'loading'
})
setTimeout(() => {
this.$vux.loading.hide()
this.index01 = index
}, 400)
},
},
components: {
stickySlot,
Tab,
TabItem,
Swiper,
SwiperItem,
},
mounted() {

}

}



