label:123,
label:456,
label:789
}]
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem
},
methods: {
switchTabItem (index) {
console.log('on-before-index-change', index)
if (index === 0) {
this.$router.push({
path: ''
})
}
this.$vux.loading.show({
text: 'loading'
})
setTimeout(() => {
this.$vux.loading.hide()
this.index01 = index
}, 1000)
},
chooseMenu (index) {
if (index === 0) {
this.$router.push({
path: 'scroll'
})
}else if(index === 1) {
this.$router.push({
path:"statisticsdata"
})
}else if(index === 2) {
this.$roter.push({
path:"cloudstorage"
})
}
}
}
}



